"use client"

import { useEffect, useRef, useState, type FormEvent } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Send, Sparkles, User, Bot } from "lucide-react"
import { useLanguage } from "@/lib/language-context"

type ChatMessage = {
  id: string
  role: "user" | "assistant"
  text: string
}

const STORAGE_KEY = "tn-chat-messages"

export function ChatInterface() {
  const { t } = useLanguage()
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const inputRef = useRef<HTMLInputElement>(null)
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [hydrated, setHydrated] = useState(false)

  useEffect(() => {
    if (typeof window === "undefined") return
    try {
      const saved = localStorage.getItem(STORAGE_KEY)
      if (saved) {
        const parsed = JSON.parse(saved) as ChatMessage[]
        setMessages(parsed)
      }
    } catch {
      // ignore parse errors
    } finally {
      setHydrated(true)
    }
  }, [])

  useEffect(() => {
    if (!hydrated || typeof window === "undefined") return
    localStorage.setItem(STORAGE_KEY, JSON.stringify(messages))
  }, [messages, hydrated])

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [messages])

  const sendToAbacus = async (history: ChatMessage[]) => {
    const payload = {
      messages: history.map((m) => ({ role: m.role, text: m.text })),
    }

    const resp = await fetch("/api/chat", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    })

    if (!resp.ok) {
      const errorText = await resp.text()
      throw new Error(errorText || "Erro ao contatar o agente")
    }

    const data = await resp.json()
    const replyText = data?.reply || t("typing")

    const assistantMsg: ChatMessage = {
      id: crypto.randomUUID(),
      role: "assistant",
      text: replyText,
    }

    return assistantMsg
  }

  const handleSubmit = async (e: FormEvent<HTMLFormElement> | { preventDefault: () => void }) => {
    e.preventDefault()
    const input = inputRef.current?.value
    if (!input?.trim() || isLoading) return

    const userMsg: ChatMessage = {
      id: crypto.randomUUID(),
      role: "user",
      text: input.trim(),
    }

    const nextHistory = [...messages, userMsg]
    setMessages(nextHistory)
    setIsLoading(true)

    if (inputRef.current) {
      inputRef.current.value = ""
    }

    try {
      const assistantMsg = await sendToAbacus(nextHistory)
      setMessages((prev) => [...prev, assistantMsg])
    } catch (err) {
      const assistantMsg: ChatMessage = {
        id: crypto.randomUUID(),
        role: "assistant",
        text: "Desculpe, nÃ£o consegui responder agora. Tente novamente em instantes.",
      }
      setMessages((prev) => [...prev, assistantMsg])
      console.error(err)
    } finally {
      setIsLoading(false)
    }
  }

  const handleSuggestedQuestion = async (question: string) => {
    if (inputRef.current) {
      inputRef.current.value = question
    }
    const fakeEvent = { preventDefault: () => {} } as unknown as React.FormEvent<HTMLFormElement>
    await handleSubmit(fakeEvent)
  }

  const suggestedQuestions = [t("q1"), t("q2"), t("q3"), t("q4")]

  const isEmpty = messages.length === 0

  return (
    <Card className="mx-auto w-full overflow-hidden border-2">
      <div className="flex items-center gap-2 border-b bg-muted/30 px-4 py-3">
        <Sparkles className="h-5 w-5 text-primary" />
        <span className="text-sm font-semibold sm:text-base">{t("aiAssistant")}</span>
        <span className="ml-auto text-xs text-muted-foreground">{isLoading ? t("typing") : t("online")}</span>
      </div>

      <div className="h-[400px] overflow-y-auto p-3 sm:h-[500px] sm:p-4">
        {isEmpty ? (
          <div className="flex h-full flex-col items-center justify-center gap-4">
            <div className="rounded-full bg-primary/10 p-4">
              <Bot className="h-8 w-8 text-primary" />
            </div>
            <div className="text-center">
              <h3 className="mb-2 text-sm font-semibold sm:text-base">{t("startConversation")}</h3>
              <p className="mb-4 text-xs text-muted-foreground sm:text-sm">{t("askAnything")}</p>
            </div>
            <div className="flex flex-wrap justify-center gap-2">
              {suggestedQuestions.map((question, index) => (
                <Button
                  key={index}
                  variant="outline"
                  size="sm"
                  onClick={() => handleSuggestedQuestion(question)}
                  className="text-xs"
                  disabled={isLoading}
                >
                  {question}
                </Button>
              ))}
            </div>
          </div>
        ) : (
          <div className="space-y-4">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex gap-3 ${message.role === "user" ? "justify-end" : "justify-start"}`}
              >
                {message.role === "assistant" && (
                  <div className="flex h-8 w-8 shrink-0 items-center justify-center rounded-full bg-primary/10">
                    <Bot className="h-4 w-4 text-primary" />
                  </div>
                )}
                <div
                  className={`max-w-[85%] rounded-lg px-3 py-2 sm:max-w-[80%] sm:px-4 ${
                    message.role === "user" ? "bg-primary text-primary-foreground" : "bg-muted"
                  }`}
                >
                  <p className="text-sm leading-relaxed whitespace-pre-wrap">{message.text}</p>
                </div>
                {message.role === "user" && (
                  <div className="flex h-8 w-8 shrink-0 items-center justify-center rounded-full bg-secondary/10">
                    <User className="h-4 w-4 text-secondary" />
                  </div>
                )}
              </div>
            ))}
            <div ref={messagesEndRef} />
          </div>
        )}
      </div>

      <form onSubmit={handleSubmit} className="border-t p-3 sm:p-4">
        <div className="flex gap-2">
          <Input
            ref={inputRef}
            placeholder={t("askPlaceholder")}
            disabled={isLoading}
            className="flex-1"
            autoComplete="off"
          />
          <Button type="submit" size="icon" disabled={isLoading}>
            <Send className="h-4 w-4" />
            <span className="sr-only">{t("send")}</span>
          </Button>
        </div>
      </form>
    </Card>
  )
}
